.hownd-list-charities {
  font-size: 16px;
  color: #1c2b42;
}
.hownd-list-charities__col {
  padding-right: 15px;
  padding-left: 15px;
  margin-bottom: 40px;
}
.hownd-list-charities__image {
  display: flex;
  justify-self: center;
  align-items: center;
  margin-bottom: 30px;
  width: 100%;
  height: 360px;
}
.hownd-list-charities__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.hownd-list-charities__image.placeholder {
  background-color: #f2f2f2;
}
.hownd-list-charities__title {
  font-size: 30px;
  text-align: center;
}